.MainBar{
  /* padding: 50px; */
  border: 1px solid papayawhip;
  padding-top: 30px;
  text-align: left;
  margin-top: 20px;
  vertical-align: top;
  display: inline-block;
   width: 52vw;
   margin-left: 25px;
  /* margin-right: 50px; */
}
.Home,.Contact,.Project,.Profile,.Skills{
  padding: 0px;
  border: 1px solid papayawhip;
  display:block;
  text-align: center;
  margin-top: 20px;
  width: auto;
  margin-bottom: 0px;
}


.Labels
{
  color: white;
  padding: 8px;
  background-color:#2A4850;
 text-shadow: 1px 1px black;
 border-style: inset;
 border-color: silver;
 margin-left: 2px;
 margin-top: 10px;
 margin-bottom: 10px;
 transition: ease-in-out;
 border-radius: 20%;
}
.profile {
  border-radius: 30%;
  height: 150px;
  width: 150px;
}
.RightBar{
  padding-top: 30px;
  /* border: 1px solid papayawhip; */
  display: inline-block;
  margin-left: 25px;
  text-align: left;
  margin-top: 20px;
  vertical-align: top;
  margin-right: 25px;
  width: 250px;
}
.RightLinks{
  text-decoration: none;
  color: white;
  background-color:#2A4850;
  border-style: inset;
 border-radius:20%;
 height: 50px;
margin-bottom: 15px;
 list-style-type: none;
}
.RightLinks:hover{
  background-color: #282c34;
  transform: rotateX(360deg);
  transition: transform 2s;
}
.headicons{
  width: 45px;
  height: 45px;
  border-radius: 50%;
}
.headicons:hover{
  transform: scaleX(-1);
}
.Logos
{
  margin-left: 5px;
  width: 30px;
  height: 30px;
  border-radius: 30%;
}
.Logos:hover{
  border-radius: 50px;
}
.App{
  width: 100vw;
  height: 100%;
  /* border: 2px solid white; */
  margin-top: 20px;
}
